.admin_table_wrapper {
    background: rgb(210, 210, 210);
    color: rgb(16, 1, 1);
    width: 100%;
    overflow-x: scroll;

    table {
        width: 100%;
        text-align: center;
        // background: red;
    }

    table,
    tr,
    th,
    td {
        border: 1px solid;
    }

    tr:nth-child(odd) {
        background: rgb(189, 189, 189);
    }
    tr:nth-child(even) {
        background: rgb(160, 75, 75);
    }

    .checkbox_wrapper {
        width: 100%;
        height: 100%;
        display: inline-flex;
        justify-content: center;
        align-items: center;

        input[type="checkbox"] {
            margin: 0;
            padding: 0;
        }
    }
}
.reload {
    cursor: pointer;
    border: 2px solid tomato;
    text-transform: capitalize;
    color: white;
    font-size: 18px;
    box-sizing: border-box;
    padding: 3px;
    user-select: none;
}
